
<template>
  <section class="wh_container">
    <div class="wh_content_all">
      <div class="wh_top_changge">
        <li @click="PreMonth(myDate,false)">
          <div class="wh_jiantou1"></div>
        </li>
        <li class="wh_content_li">{{dateTop}}</li>
        <li @click="NextMonth(myDate,false)">
          <div class="wh_jiantou2"></div>
        </li>
      </div>
      <div class="wh_content">
        <div class="wh_content_item" v-for="(tag,index) in textTop" :key="index">
          <div class="wh_top_tag">{{tag}}</div>
        </div>
      </div>
      <div
        class="wh_content wh_content_week"
        :class="{'wh_content_week_active':week.IsSelected}"
        v-for="(week,i) in list"
        @click="clickWeek(week,i)"
        :key="i"
      >
        <div
          class="wh_content_item"
          v-for="(item,index) in week.Days"
          @click="clickDay(item,index)"
          :key="index"
        >
          <div
            class="wh_item_date"
            v-bind:class="[{ wh_isMark: item.isMark},{wh_other_dayhide:item.otherMonth!=='nowMonth'},{wh_want_dayhide:item.dayHide},{wh_isToday:item.isToday},{wh_chose_day:item.chooseDay},setClass(item)]"
          >{{item.id}}</div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  /* eslint-disable */
  data() {
    return {
      myDate: [],
      list: [],
      historyChose: [],
      dateTop: "",
      weekStart: "",
      weekEnd: ""
    };
  },
  props: {
    markDate: {
      type: Array,
      default: () => []
    },
    markDateMore: {
      type: Array,
      default: () => []
    },
    textTop: {
      type: Array,
      default: () => ["一", "二", "三", "四", "五", "六", "日"]
    },
    sundayStart: {
      type: Boolean,
      default: () => false
    },
    agoDayHide: {
      type: String,
      default: `0`
    },
    futureDayHide: {
      type: String,
      default: `2554387200`
    }
  },
  created() {
    this.intStart();
    this.myDate = new Date();
  },
  methods: {
    // 当某月的天数
    getDaysInOneMonth(date) {
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const d = new Date(year, month, 0);
      return d.getDate();
    },
    // 向前空几个
    getMonthweek(date) {
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const dateFirstOne = new Date(year + "/" + month + "/1");
      return this.sundayStart
        ? dateFirstOne.getDay() == 0
          ? 7
          : dateFirstOne.getDay()
        : dateFirstOne.getDay() == 0
        ? 6
        : dateFirstOne.getDay() - 1;
    },
    /**
     * 获取当前日期上个月或者下个月
     */
    getOtherMonth(date, str = "nextMonth") {
      const timeArray = this.dateFormat(date).split("/");
      const year = timeArray[0];
      const month = timeArray[1];
      const day = timeArray[2];
      let year2 = year;
      let month2;
      if (str === "nextMonth") {
        month2 = parseInt(month) + 1;
        if (month2 == 13) {
          year2 = parseInt(year2) + 1;
          month2 = 1;
        }
      } else {
        month2 = parseInt(month) - 1;
        if (month2 == 0) {
          year2 = parseInt(year2) - 1;
          month2 = 12;
        }
      }
      let day2 = day;
      const days2 = new Date(year2, month2, 0).getDate();
      if (day2 > days2) {
        day2 = days2;
      }
      if (month2 < 10) {
        month2 = "0" + month2;
      }
      if (day2 < 10) {
        day2 = "0" + day2;
      }
      const t2 = year2 + "/" + month2 + "/" + day2;
      return new Date(t2);
    },
    // 上个月末尾的一些日期
    getLeftArr(date) {
      const arr = [];
      const leftNum = this.getMonthweek(date);
      const num =
        this.getDaysInOneMonth(this.getOtherMonth(date, "preMonth")) -
        leftNum +
        1;
      const preDate = this.getOtherMonth(date, "preMonth");
      // 上个月多少开始
      for (let i = 0; i < leftNum; i++) {
        const nowTime =
          preDate.getFullYear() +
          "/" +
          (preDate.getMonth() + 1) +
          "/" +
          (num + i);
        arr.push({
          id: num + i,
          date: nowTime,
          isToday: false,
          otherMonth: "preMonth"
        });
      }
      return arr;
    },
    // 下个月末尾的一些日期
    getRightArr(date) {
      const arr = [];
      const nextDate = this.getOtherMonth(date, "nextMonth");
      const leftLength = this.getDaysInOneMonth(date) + this.getMonthweek(date);
      const _length = 7 - (leftLength % 7);
      for (let i = 0; i < _length; i++) {
        const nowTime =
          nextDate.getFullYear() +
          "/" +
          (nextDate.getMonth() + 1) +
          "/" +
          (i + 1);
        arr.push({
          id: i + 1,
          date: nowTime,
          isToday: false,
          otherMonth: "nextMonth"
        });
      }
      return arr;
    },
    // format日期
    dateFormat(date) {
      date =
        typeof date === "string" ? new Date(date.replace(/\-/g, "/")) : date;
      return (
        date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate()
      );
    },
    // 获取某月的列表不包括上月和下月
    getMonthListNoOther(date) {
      const arr = [];
      const num = this.getDaysInOneMonth(date);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const toDay = this.dateFormat(new Date());

      for (let i = 0; i < num; i++) {
        const nowTime = year + "/" + month + "/" + (i + 1);
        arr.push({
          id: i + 1,
          date: nowTime,
          isToday: toDay === nowTime,
          otherMonth: "nowMonth"
        });
      }
      return arr;
    },
    // 获取某月的列表 用于渲染
    getMonthList(date) {
      return [
        ...this.getLeftArr(date),
        ...this.getMonthListNoOther(date),
        ...this.getRightArr(date)
      ];
    },
    intStart() {
      this.sundayStart = this.sundayStart;
    },
    setClass(data) {
      let obj = {};
      obj[data.markClassName] = data.markClassName;
      return obj;
    },
    clickWeek: function(item, index) {
      this.list.forEach((week, i) => {
        week.IsSelected = i == index;
      });

      this.weekStart = item.Days[0].date;
      this.weekEnd = item.Days[item.Days.length - 1].date;

      this.$emit("changeSelectedWeek", {
        weekStart: this.weekStart,
        weekEnd: this.weekEnd
      });
    },
    clickDay: function(item, index) {
      return;
      // if (item.otherMonth === "nowMonth" && !item.dayHide) {
      //   this.getList(this.myDate, item.date);
      // }
      // if (item.otherMonth !== "nowMonth") {
      //   item.otherMonth === "preMonth"
      //     ? this.PreMonth(item.date)
      //     : this.NextMonth(item.date);
      // }
    },
    ChoseMonth: function(date, isChosedDay = true) {
      date = this.dateFormat(date);
      this.myDate = new Date(date);
      this.$emit("changeMonth", this.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    PreMonth: function(date, isChosedDay = true) {
      date = this.dateFormat(date);
      this.myDate = this.getOtherMonth(this.myDate, "preMonth");
      this.$emit("changeMonth", this.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    NextMonth: function(date, isChosedDay = true) {
      date = this.dateFormat(date);
      this.myDate = this.getOtherMonth(this.myDate, "nextMonth");
      this.$emit("changeMonth", this.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    forMatArgs: function() {
      let markDate = this.markDate;
      let markDateMore = this.markDateMore;
      let that = this;
      markDate = markDate.map(k => {
        return that.dateFormat(k);
      });
      markDateMore = markDateMore.map(k => {
        k.date = that.dateFormat(k.date);
        return k;
      });
      return [markDate, markDateMore];
    },
    getList: function(date, chooseDay, isChosedDay = true) {
      const [markDate, markDateMore] = this.forMatArgs();
      this.dateTop = `${date.getFullYear()}年${date.getMonth() + 1}月`;
      let arr = this.getMonthList(this.myDate);
      let weeks = [];

      var now = new Date(); //当前日期
      var nowDayOfWeek = now.getDay(); //今天本周的第几天
      var nowDay = now.getDate(); //当前日
      var nowMonth = now.getMonth(); //当前月
      var nowYear = now.getYear(); //当前年
      nowYear += nowYear < 2000 ? 1900 : 0; //

      if (this.weekStart == "") {
        this.weekStart = this.dateFormat(
          new Date(nowYear, nowMonth, nowDay - nowDayOfWeek + 1)
        );
      }
      if (this.weekEnd == "") {
        this.weekEnd = this.dateFormat(
          new Date(nowYear, nowMonth, nowDay + (7 - nowDayOfWeek))
        );
      }

      for (let i = 0; i < arr.length; i++) {
        let markClassName = "";
        let k = arr[i];
        k.chooseDay = false;
        k.weekIndex = Math.floor(i / 7);
        const nowTime = k.date;
        const t = new Date(nowTime).getTime() / 1000;
        //看每一天的class
        for (const c of markDateMore) {
          if (c.date === nowTime) {
            markClassName = c.className || "";
          }
        }
        //标记选中某些天 设置class
        k.markClassName = markClassName;
        k.isMark = markDate.indexOf(nowTime) > -1;
        //无法选中某天
        k.dayHide = t < this.agoDayHide || t > this.futureDayHide;
        if (k.isToday) {
          this.$emit("isToday", nowTime);
        }
        let flag = !k.dayHide && k.otherMonth === "nowMonth";
        if (chooseDay && chooseDay === nowTime && flag) {
          this.$emit("choseDay", nowTime);
          this.historyChose.push(nowTime);
          k.chooseDay = true;
        } else if (
          this.historyChose[this.historyChose.length - 1] === nowTime &&
          !chooseDay &&
          flag
        ) {
          k.chooseDay = true;
        }

        if (weeks.length <= k.weekIndex) {
          let selectedWeek = this.weekStart == k.date;

          weeks.push({ IsSelected: selectedWeek, Days: [] });
        }

        weeks[k.weekIndex].Days.push(k);
      }
      this.list = weeks;
    }
  },
  mounted() {
    this.getList(this.myDate);
  },
  watch: {
    markDate: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    markDateMore: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    agoDayHide: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    futureDayHide: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    sundayStart: {
      handler(val, oldVal) {
        this.intStart();
        this.getList(this.myDate);
      },
      deep: true
    }
  }
};
</script>
<style scoped>
@media screen and (min-width: 460px) {
  .wh_item_date:hover {
    /* background: #71c7a5; */
    cursor: pointer;
  }
}
* {
  margin: 0;
  padding: 0;
}
.wh_container {
  max-width: 410px;
  margin: auto;
}
li {
  list-style-type: none;
}
.wh_top_changge {
  display: flex;
}
.wh_top_changge li {
  cursor: pointer;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  color: #333333;
  font-size: 14px;
  height: 40px;
}
.wh_top_changge .wh_content_li {
  cursor: auto;
  flex: 2.5;
}
.wh_content_all {
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC",
    "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
  background-color: #fff;
  width: 100%;
  overflow: hidden;
  padding-bottom: 8px;
}
.wh_content {
  display: flex;
  flex-wrap: wrap;
  padding: 0 3% 0 3%;
  width: 100%;
}
.wh_content:first-child .wh_content_item_tag,
.wh_content:first-child .wh_content_item {
  color: #ddd;
  font-size: 16px;
}
.wh_content_item,
.wh_content_item_tag {
  font-size: 15px;
  text-align: center;
  position: relative;
}
.wh_content_item {
  margin-top: 2px;
  margin-bottom: 2px;
  color: #333;
  width: 14.285714285714285714285714285714%;
  height: 24px;
}
.wh_top_tag {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.wh_item_date {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.wh_jiantou1 {
  width: 10px;
  height: 10px;
  border-top: 2px solid #333333;
  border-left: 2px solid #333333;
  transform: rotate(-45deg);
}
.wh_jiantou1:active,
.wh_jiantou2:active {
  border-color: #ddd;
}
.wh_jiantou2 {
  width: 10px;
  height: 10px;
  border-top: 2px solid #333333;
  border-right: 2px solid #333333;
  transform: rotate(45deg);
}
.wh_content_item > .wh_isMark {
  color: #333;
  border-radius: 0px;
  background-color: rgba(0, 169, 247, 0.2);
  margin: auto;
  /* border-radius: 100px; */
  /* background: blue; */
  z-index: 2;
}
.wh_content_item .wh_other_dayhide {
  color: #bfbfbf;
}
.wh_content_item .wh_want_dayhide {
  color: #999;
}
.wh_content_week_active .wh_item_date {
  background-color: rgba(0, 169, 247, 0.2);
}
.wh_content_item .wh_isToday {
  color: #fff;
  /* border-radius: 0px; */
  background-color: #45aff9;
}
/* .wh_content_item .wh_chose_day {
   background: green; 
   border-radius: 100px; 
} */
.wh_content_week:hover .wh_item_date {
  background-color: rgba(69, 174, 249, 0.1);
}
.wh_content_week:hover .wh_isToday {
  background-color: #45aff9;
}
</style>


