<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的评价</div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="待评价" name="first"></el-tab-pane>
      <el-tab-pane label="已评价" name="second"></el-tab-pane>
      <el-tab-pane label="评价计划" name="third"></el-tab-pane>
      <el-tab-pane label="被评价课程" name="fourth"></el-tab-pane>
    </el-tabs>
    <span class="viewchange fr" @click="gotoList">
      <i class="iconfont icon-baoming" /> 按列表查看
    </span>
    <div class="cbox">
      <div class="cbox-l">
        <Calendar ref="Calendar" v-on:changeSelectedWeek="changeSelectedWeek"></Calendar>
        <div class="cbox-l-b">
          <div>
            <template>
              <div class="checkitem" @click="checkAll">全选</div>
              <div class="checkitem" @click="uncheck">反选</div>
              <div class="teacher-evaluate-card-content-top-button1" @click="searchHandler">查询</div>
            </template> 
          </div>
          <div>
          <p>待评价老师</p>
          <div v-for="(thc,i) in Teachers" :key="i" class="thcitem">
            <el-checkbox :label="thc.name" v-model="thc.checked" :checked="thc.checked" @change="checkchange(thc)"></el-checkbox>
          </div>
          </div>
        </div>
        <div class="clear" />
      </div>
      <div class="cbox-r">
        <p>{{weekStart}} — {{weekEnd}} 待评价课表 共{{totalless}}节</p>
        <div class="personnal-detail-wrap-container">
          <evaluate-card
            v-for="(c,i) in cards"
            @GetInto="GetInto"
            :key="i"
            :card="c"
            :btnName="btnName"
            ref="evaluate"
          />
          <div v-if="cards.length==0" style="margin-top:30px;">{{message}}</div>
        </div>
        <bottom-pager :total="total" :pageSize="pageSize" @change="pageChange" ref="bottomPager" />
      </div>
    </div>
    <div class="clear" />
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import { formatDate } from "../../../../utils/utils.js";
import evaluateCard from "../../../../components/personal/teacher/evaluateCard.vue";
import bottomPager from "../../../../components/personal/bottomPager.vue";
import Calendar from "../../../../components/calendar_Week"; //引入vue日历
export default {
  components: { Calendar, evaluateCard, bottomPager },
  data() {
    return {
      radio: 0,
      btnName: "进入",
      activeName: "first",
      Teachers: [],
      message: "暂无数据",
      weekStart: "",
      weekEnd: "",
      cards: [],
      total: 0,
      paginalNumber: 1,
      pageSize: http.pageSize,
      ThcIDs: "",
      totalless: 0
    };
  },
  mounted() {
    if (http.logined()) {
      // 已登录
      let userInfo = http.getUser();
      if (userInfo) {
        this.userid = http.getUser().UserID;
      }
    }

    var now = new Date(); //当前日期
    var nowDayOfWeek = now.getDay(); //今天本周的第几天
    var nowDay = now.getDate(); //当前日
    var nowMonth = now.getMonth(); //当前月
    var nowYear = now.getYear(); //当前年
    nowYear += nowYear < 2000 ? 1900 : 0; //

    if (this.weekStart == "") {
      this.weekStart = this.$refs.Calendar.dateFormat(
        new Date(nowYear, nowMonth, nowDay - nowDayOfWeek + 1)
      );
    }
    if (this.weekEnd == "") {
      this.weekEnd = this.$refs.Calendar.dateFormat(
        new Date(nowYear, nowMonth, nowDay + (7 - nowDayOfWeek))
      );
    }
    this.Teachers = [];
    this.cards = [];
    this.GetThcLists();
    this.Getdatas();
  },
  methods: {
    searchHandler() {
      this.cards = [];
      this.Getdatas();
    },
    checkAll() {
      if (this.Teachers.length > 0) {
        var thcids = "";
        this.Teachers.forEach(element => {
          element.checked = true;
          thcids += "'" + element.id + "',";
        });
        this.ThcIDs = thcids;
      }
    },
    uncheck() {
      if (this.Teachers.length > 0) {
        var thcids = "";
        this.Teachers.forEach(element => {
          if (element.checked == true) element.checked = false;
          else {
            element.checked = true;
            thcids += "'" + element.id + "',";
          }
        });
        this.ThcIDs = thcids;
      }
    },
    gotoList() {
      this.$router.push({
        path: "/main/teacherSpace/teacherEvaluate/list",
        query: {
          tabname: "first"
        }
      });
    },
    tabClick(val) {
      if (val != "first") {
        this.$router.push({
          path: "/main/teacherSpace/teacherEvaluate/list",
          query: {
            tabname: val.name
          }
        });
      }
    },
    changeSelectedWeek(weekInfo) {
      this.weekStart = weekInfo.weekStart;
      this.weekEnd = weekInfo.weekEnd;
      this.Teachers = [];
      this.cards = [];
      this.GetThcLists();
      this.Getdatas();
    },
    async GetThcLists() {
      this.Teachers = [];
      const res = await http.get(api.GetThcListByWeek, {
        uid: this.userid,
        startDate: this.weekStart,
        endDate: this.weekEnd,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
        this.message = "";
      } else {
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(ele => {
            this.Teachers.push({
              name: ele.TeacherName,
              checked: false,
              id: ele.TeacherID
            });
          });
        }
      }
    },
    async Getdatas() {
      if (this.ThcIDs == "") return;
      this.message = "数据加载中...";
      const res = await http.get(api.GetThcRatingRecTaskByWeek, {
        uid: this.userid,
        startDate: this.weekStart,
        endDate: this.weekEnd,
        ThcIDs: this.ThcIDs,
        pno: this.paginalNumber,
        psize: this.pageSize,
        sfield: "",
        sdir: "",
        estate: "",
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
        this.message = "";
      } else {
        this.message = "";
        this.cards = [];
        this.total = 0;
        this.totalless = 0;
        if (res.Datas != null && res.Datas.length > 0) {
          this.totalless = res.RowsCount;
          this.total = res.RowsCount;
          res.Datas.forEach(element => {
            let desc = [];
            let PlanDetail = null;
            let erange = "";
            switch (element.Value[0].EVALPlan.EVALPRange) {
              case 0:
                erange = "不限";
                break;
              case 1:
                erange = "按老师";
                break;
              case 2:
                erange = "按部门";
                break;
            }
            let evalpClassify = "";
            switch (element.Value[0].EVALPlan.EVALPClassify) {
              case "1":
                evalpClassify = "百门优质课程评审";
                break;
              case "2":
                evalpClassify = "精彩一堂课";
                break;
              case "3":
                evalpClassify = "职称评定";
                break;
              case "4":
                evalpClassify = "通识教育选修课";
                break;
              case "5":
                evalpClassify = "全英文课程";
                break;
              case "6":
                evalpClassify = "翻转课堂";
                break;  
               case "7":
                evalpClassify = "随机听课";
                break;     
            }
            PlanDetail = {
              lessonsDate: formatDate(
                new Date(element.Value[0].lessonTime),
                "yyyy-MM-dd"
              ),
              lesson: "第 " + element.Key.CSLesson + " 节",
              clsroomname: element.Value[0].ClsRoomName,
              teachername: element.Value[0].TeacherName,
              coursename: element.Value[0].CourseName,
              classname: element.Value[0].ClassName,
              PlanDateRane:
                formatDate(
                  element.Value[0].EVALPlan.EVALPStart,
                  "yyyy年MM月dd日"
                ) +
                "至" +
                formatDate(
                  element.Value[0].EVALPlan.EVALPEnd,
                  "yyyy年MM月dd日"
                ),
              EVALPCount: element.Value[0].EVALPlan.EVALPCount,
              EVALPRange: erange 
            };
            var tylename = "";
            switch (element.Value[0].EVALPlan.EVALPType) {
              case 1:
                tylename = "课表";
                break;
              case 2:
                tylename = "老师";
                break;
              case 3:
                tylename = "课程";
                break;
            }
            var canenter = true;
            var csstart = new Date(element.Value[0].lessonTime).getTime();
            var nowtime = new Date().getTime();
            if (element.Key.EVALPType == 1 && csstart > nowtime) {
              canenter = false;
            }
            this.cards.push({
              id: element.Value[0].id,
              csid: element.Key.CSID,
              eID: element.Value[0].EVALPlan.EVALPID,
              gID: element.Value[0].EVALPlan.GaugeID,
              title: element.Value[0].EVALPlan.EVALPName,
              className: element.Value[0].ClassName,
              clsName: element.Value[0].ClsRoomName,
              courseName: element.Value[0].CourseName,
              csDate: formatDate(element.Value[0].lessonTime, "yyyy-MM-dd"),
              teacherID: element.Value[0].TeacherID,
              teachername: element.Value[0].TeacherName,
              stardate: formatDate(
                element.Value[0].EVALPlan.EVALPStart,
                "yyyy-MM-dd"
              ),
              enddate: formatDate(
                element.Value[0].EVALPlan.EVALPEnd,
                "yyyy-MM-dd"
              ),
              pjcount: element.Value[0].pcount,
              subject: "",
              evlapType: 1,
              csLesson: element.Key.CSLesson,
              type: tylename,
              tabindex: 1,
              canenter: canenter,
              uid: "",
              range: 0,
              lessonsTime: element.Value[0].lessonTime,
              descs: desc,
              evalpClassify: evalpClassify,
              PlanDetail: PlanDetail
            });
          });
        }
      }
    },
    pageChange(page) {
      this.paginalNumber = page;
      this.cards = [];
      this.Getdatas();
    },
    GetInto(row) {
      var csstart = new Date(row.lessonsTime).getTime();
      var nowtime = new Date().getTime();
      if (csstart > nowtime) {
        this.$message({
          showClose: true,
          message: "当前时间小于开始上课时间，无法进入评价",
          type: "warning"
        });
        return;
      }
      this.$router.push({
        name: `detail`,
        query: {
          id: row.csid,
          gid: row.gID,
          eid: row.eID,
          uid: this.userid,
          className: row.className,
          clsRoom: row.clsName,
          courseName: row.courseName,
          csDate: row.lessonsTime,
          thcName: row.teacherName,
          thcID: row.teacherID,
          lesson: row.csLesson
        }
      });
    },
    checkchange(val) {
      var thcids = "";
      this.Teachers.forEach(ele => {
        if (ele.checked == true) {
          thcids += "'" + ele.id + "',";
        }
      });
      this.ThcIDs = thcids;
      this.redio = 0;
    }
  }
};
</script>
<style lang="less">
.viewchange {
  margin-top: -28px;
  cursor: pointer;
  position: relative;
  z-index: 100;
}
.cbox {
  display: inline-block;
  width: 100%;
  margin-top: 20px;
  border: solid 1px #dddddd;
  .cbox-l,
  .cbox-r {
    float: left;
  }
  .cbox-l {
    padding-top: 13px;
    height: 100%;
    width: 220px;
    border-right: solid 1px #dddddd;
    .cbox-l-b {
      text-align: left;
      margin-top: 20px;
      padding: 20px;
      border-top: solid 1px #dddddd;

      p {
        margin-bottom: 20px;
      }

      .thcitem {
        margin: 6px;
      }
    }
  }
  .cbox-r {
    width: calc(100% - 222px);
    padding: 20px;
    p {
      text-align: left;
    }
  }
}
.teacher-evaluate-card {
  width: 100%;
  //height: 92px;
  // border-bottom: 2px solid #d1d9e3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .iconfont {
    font-size: 47px;
    color: #2b9efc;
  }
  &-content {
    width: 100%;
    margin-left: 20px;
    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-align: left;
      &-button1 {
        width: 60px;
        height: 30px;
        line-height: 30px;
        color: #fff;
        background: #2b9efc;
        border-radius: 3px;
        user-select: none;
        cursor: pointer;
        text-align: center;
        float: left;
        margin-right: 18px;
      }
    }
  }
}
.checkitem {
  float: left;
  margin-right: 18px;
  color: #2b9efc;
  cursor: pointer;
}
</style>



