<template>
  <el-card shadow="hover" body-style="padding:10px">
    <div class="teacher-evaluate-card">
      <i class="iconfont icon-pingjia1"></i>
      <div class="teacher-evaluate-card-content">
        <div class="teacher-evaluate-card-content-top">
          <div class="teacher-evaluate-card-content-top-Info">
            <div class="teacher-evaluate-card-content-top-Info-top">
              <span class="teacher-evaluate-card-content-Info-top-title">{{card.title}}</span>
              <el-tag
                size="mini"
                effect="dark"
                :type="(card.evlapType == 1 ? '' : (card.evlapType == 2 ? 'success' : 'warning'))"
              >{{(card.evlapType == 1 ? '课表' : (card.evlapType == 2 ? '教师' : '学科'))}}</el-tag>
              <span v-if="card.rfcsid == 1" class="title-span">线下课</span>
              <!-- <span>已评课次数：{{card.pjcount}} 次</span>-->
            </div>
            <div class="teacher-evaluate-card-content-top-Info-bottom">
              <p v-if="card.evlapType == 1" class="flex">
                <span v-if="card.PlanDetail.teachername">授课教师：{{card.PlanDetail.teachername}}</span>
                <span v-if="card.PlanDetail.clsroomname">教室：{{card.PlanDetail.clsroomname}}</span>
              </p>
              <p v-if="card.evlapType == 1" class="flex">
                <span v-if="card.PlanDetail.coursename">教学课程：{{card.PlanDetail.coursename}}</span>
                <span
                  v-if="card.PlanDetail.lessonsDate"
                >排课时间：{{card.PlanDetail.lessonsDate}} {{card.PlanDetail.lesson}}</span>
              </p>
              <p v-if="card.evlapType == 1" class="flex">  
                 <span >评价范围：{{card.PlanDetail.EVALPRange}}</span>  
                <span v-if="card.evalpClassify">评课种类：{{card.evalpClassify}}</span>
              </p> 
              <p v-if="card.evlapType == 1">
                <span v-if="card.PlanDetail.classname">上课班级：{{card.PlanDetail.classname}}</span>
              </p>
              <p v-if="card.evlapType != 1" class="flex">
                <span v-if="card.PlanDetail.objname">评价对象：{{card.PlanDetail.objname}}</span>
                <span v-if="card.evalpClassify">评课种类：{{card.evalpClassify}}</span>
              </p> 
              <!-- <div
            class="teacher-evaluate-card-content-Info-bottom-split fl"
            v-for="(c,i) in descs"
            :key="i"
              >{{c}}</div>-->
            </div>
          </div>
          <div
            class="teacher-evaluate-card-content-top-score"
            v-if="card.tabindex == 2"
            :class="{'red-score':card.score < 60,'blue-score':card.score >= 60}"
          >{{card.score}}</div>
          <div v-if="card.rfestate!=3"
            class="teacher-evaluate-card-content-top-button"
            @click="enterHandler(card.type)"
          >{{btnName}}</div>
           <div v-if="card.rfestate==3"
            class="teacher-evaluate-card-content-top-button1"
            @click="enterHandler(card.type)"
          >{{btnName}}</div>
        </div>

        <div class="teacher-evaluate-card-content-bottom">
          <p class="flex">
            <span v-if="card.rfcreatedate!='' && card.day<7  && card.zt==1">评价时间：<span style="color:#f58500;">{{card.rfcreatedate}}</span></span>
            <span v-if="card.rfcreatedate!='' && card.day<7  && card.zt!=1">评价时间：{{card.rfcreatedate}}</span>
            <span v-if="card.rfcreatedate!='' && card.day>=7">评价时间：{{card.rfcreatedate}}</span>
            <span v-if="card.rfcreatedate=='' && card.PlanDetail.PlanDateRane!='' && card.day<7 && card.zt==1">评价时间：<span style="color:#f58500;">{{card.PlanDetail.PlanDateRane}}</span></span>
            <span v-if="card.rfcreatedate=='' && card.PlanDetail.PlanDateRane!='' && card.day<7 && card.zt!=1">评价时间：{{card.PlanDetail.PlanDateRane}}</span>
            <span v-if="card.rfcreatedate=='' && card.PlanDetail.PlanDateRane!='' && card.day>=7">评价时间：{{card.PlanDetail.PlanDateRane}}</span>
            <span v-if="card.PlanDetail.EVALPCount">计划次数：{{card.PlanDetail.EVALPCount}}</span>
            <span v-if="card.pjcount">已评次数：{{card.pjcount}}</span>
          </p>
        </div>
      </div>
    </div>
  </el-card>
</template>
<script>
export default {
  props: {
    card: {
      type: Object
    },
    btnName: {
      type: String
    }
  },
  data() {
    return {
      descs: []
    };
  },
  mounted() {
    this.descs = this.card.descs;
  },
  watch: {
    card(newVal, oldVal) {
      if (newVal) {
        this.descs = newVal.descs;
      }
    },
    btnName(newVal, oldVal) {
      if (newVal) {
        this.btnName = newVal;
      }
    }
  },
  methods: {
    enterHandler(type) {
      if (this.card.tabindex == 1) {
        if (!this.card.canenter) {
          this.$message({
            showClose: true,
            message: "当前时间小于开始上课时间，无法进入评价",
            type: "warning"
          });
          return;
        }
      }
      this.$emit("GetInto", this.card);

      /* this.$router.push(`/main/teacherSpace/teacherEvaluate/detail/${this.card.id}`); */
      // if (type === 1) {
      //   this.$router.push({
      //     name: `detail`,
      //     query: {
      //       title: this.card.title,
      //       infors: this.card.descs,
      //       recid: this.card.id,
      //       uid: this.card.uid
      //     }
      //   });
      // } else {
      //   this.$router.push({
      //     name: `evaluateddetail`,
      //     query: {
      //       title: this.card.title,
      //       evalpid: this.card.evalpid,
      //       recdate: this.card.recdate,
      //       dept: this.card.dept,
      //       teacher: this.card.descs[0]
      //     }
      //   });
      // }
    }
  }
};
</script>
<style lang="less" scoped>
.el-card {
  // border: none;
  margin: 10px auto;
  .teacher-evaluate-card {
    width: 100%;
    //height: 92px;
    // border-bottom: 2px solid #d1d9e3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .iconfont {
      font-size: 47px;
      color: #2b9efc;
    }
    &-content {
      width: 100%;
      margin-left: 20px;
      &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: left;
        &-Info {
          width: 100%;
          &-top {
            &-title {
              font-size: 18px;
              font-weight: 600;
              margin-bottom: 20px;
            }
            &-badge {
              padding: 2px 8px;
              color: #fff;
              background: #f47575;
              font-size: 0.8rem;
              border-radius: 3px;
            }
          }
          &-bottom {
            margin-top: 8px;
            .flex {
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin: 5px auto;

              span {
                width: calc(100% / 2);
              }
            }
            &-split {
              padding-right: 25px;
              padding-left: 15px;
              margin: 5px 0;
              height: 18px;
              line-height: 18px;
              border-right: 1px solid #a3a3a3;
              color: #a3a3a3;
              font-size: 12px;
              &:last-child {
                border-right: none;
                text-align: right;
              }
              &:first-child {
                text-align: left;
              }
              &:nth-child(4) {
                border-right: none;
              }
              &:nth-child(6) {
                border-right: none;
              }
              &:after {
                clear: both;
              }
            }
          }
        }
        &-button {
          width: 140px;
          height: 30px;
          line-height: 30px;
          color: #fff;
          background: #03aeff;
          border-radius: 3px;
          user-select: none;
          cursor: pointer;
          text-align: center;
        }
         &-button1 {
          width: 140px;
          height: 30px;
          line-height: 30px;
          color: #fff;
          background:#67c23a;
          border-radius: 3px;
          user-select: none;
          cursor: pointer;
          text-align: center;
        }
        &-score {
          width: 200px;
          height: 60px;
          //color: #03aeff;
          font-size: 50px;
          text-align: center;
        }
      }
      &-bottom {
        margin-top: 10px;
        border-top: solid 1px #ebebeb;
        padding: 10px 0px;
        .flex {
          display: flex;
          justify-content: space-between;
          align-items: center;

          span:last-child {
            color: #2b9efc;
          }
        }
      }
    }
  }
}
.red-score {
  color: #03aeff;
}
.blue-score {
  color: #03aeff;
}
.title-span {
  background-color: #4ea3ed;
  font-size: 12px;
  color: #fff;
  display: inline-block;
  width: 45px;
  text-align: center;
  border-radius: 5px;
}
.setcolor{
  color: f58500;
}
</style>



